import Vue from 'vue'
import Router from 'vue-router'
import RefundForm from '@/components/refund'

Vue.use(Router)

export default new Router({
  base: '/refund',
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'RefundForm',
      component: RefundForm,
      props: true
    }
  ]
})



