<template>
    <div class="container-fluid">

        <div class="row leform">
            <div class="col-sm-4 col-md-4 col-lg-4 logo">
                <a href="http://vlb.io/"><img src="/refund/static/img/vlb_logo.svg"></a>
            </div>
        </div>
        <div class="row coform">
            <div class="col-sm-4 col-md-4 col-lg-4 content">
                <h2>
                    {{ msg }}
                </h2>
            </div>
        </div>

        <div class="row coform" v-if="metamask">
            <div class="col-sm-8 col-md-8 col-lg-8 content" v-if="operation">
                <p id="passwordHelp" class="form-text text-success">{{errMes}}</p>
                <p>
                    <a v-bind:href="etherscan" target="_p">Check your transaction on Etherscan</a>
                </p>
            </div>
            <div class="col-sm-8 col-md-8 col-lg-8 content" v-else>
                <input  v-model="dataform.wallet" type="text" class="form-control" id="ETHWallet" name="wallet" placeholder="Enter your ETH Wallet" @input="checketh">
                <br/>
                <small v-if="authErr" id="passwordHelp" class="form-text text-danger">{{errMes}}</small>
                <br/>
                  <button class="btn btn-primary" id="lgnbtn" v-on:click.prevent="refund($event)" disabled>Refund my ETH</button>
            </div>
        </div>
        <div class="row coform" v-else>
            <div class="col-sm-8 col-md-8 col-lg-8 content" style="text-align: center;">
                <a href="https://metamask.io/" target="_p">
                    <img src="/refund/static/img/metamask.png" width="400">
                </a>
            </div>
        </div>
        <div class="row coform">
            <div class="col-sm-8 col-md-8 col-lg-8 content" style="text-align: center;">
                        Please read the instructions:
                        <a href="https://medium.com/vlb-token/how-to-get-a-refund-68ffc067dacb" target="_p">ENG</a> / <a href="https://medium.com/vlb-token-ru/%D0%BA%D0%B0%D0%BA-%D0%BF%D0%BE%D0%BB%D1%83%D1%87%D0%B8%D1%82%D1%8C-%D0%B2%D0%BE%D0%B7%D0%B2%D1%80%D0%B0%D1%82-%D1%81%D1%80%D0%B5%D0%B4%D1%81%D1%82%D0%B2-b43c327a99fa" target="_p">РУС</a>
            </div>
        </div>

    </div>
</template>

<script>
const etherscanAddress = 'https://etherscan.io/tx/'
export default {
  name: 'LoginForm',
  data () {
    return {
      msg: 'Refund VLB Tokens',
      output: '',
      token: '',
      dataform: {},
      authErr: false,
      errMes: '',
      metamask: false,
      abiArray: [],
      operation: false,
      etherscan: ''
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      if (typeof web3 !== 'undefined') {
        this.metamask = true
      } else {
        this.metamask = false
      }
      var self = this
      // eslint-disable-next-line
      $.getJSON('/refund/static/abi.json', function (json) {
        for (var i in json.abi) {
          self.abiArray.push(json.abi[i])
        }
      })
    },
    checketh () {
      var re = new RegExp('^0x[a-fA-F0-9]{40}$')
      if (re.test(document.getElementById('ETHWallet').value)) {
        document.getElementById('lgnbtn').disabled = false
      } else {
        document.getElementById('lgnbtn').disabled = true
      }
    },
    refund (event) {
      // web3.setProvider(new web3.providers.HttpProvider("http://localhost:8545"));
      const contractAddress = '0x2cbc6812cff0b1113bf2808ffce6d83b97afd345'
      // eslint-disable-next-line
      var localWeb3 = new Web3(window.web3.currentProvider)
      var investor = document.getElementById('ETHWallet').value
      var self = this
      self.operation = false
      self.authErr = false
      self.errMes = ''
      self.etherscan = ''
      var MyContract = localWeb3.eth.contract(this.abiArray)
      var myContractInstance = MyContract.at(contractAddress)
      myContractInstance.refund(investor, {from: localWeb3.eth.defaultAccount}, function (error, result) {
        if (!error) {
          self.operation = true
          self.errMes = 'Operation Complete!'
          self.etherscan = etherscanAddress + result
        } else {
          console.error(error)
          self.operation = false
          self.authErr = true
          self.errMes = 'Please check your ETH number'
        }
      })
    }
  }
}
</script>



